@import url(https://fonts.googleapis.com/css2?family=Be+Vietnam:wght@400;500;700&display=swap);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

:root {
  --red: hsl(12, 88%, 59%);
  --pale-red: hsl(13, 100%, 96%);
  --blue: hsl(228, 39%, 23%);
  --dark-blue: hsl(233, 12%, 13%);
  --gray: hsl(0, 0%, 35%, 1);
  --light-gray: hsl(0, 0%, 98%);
  --hover-color: rgba(89, 89, 89, 0.6);
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  height: 100vh;
  font-family: 'Be Vietnam', sans-serif;
  font-size: 62.5%;
}

h1, h2, h3, h4, p, a, span, blockquote {
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4 {
  color: hsl(228, 39%, 23%);
  color: var(--blue);
}

p, a, span, blockquote {
  color: hsl(0, 0%, 35%, 1);
  color: var(--gray);
}

a {
  list-style: none;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

figure {
  margin: 0;
  padding: 0;
}

.get-started-button {
  width: 110px;
  height: 35px;
  background-color: var(--red);
  color: var(--light-gray);
  border: none;
  border-radius: 25px;
  font-size: 12px;
  font-weight: 700;
  box-shadow: 0px 3px 7px -1px hsl(12, 88%, 59%, 0.8);
  outline: none;
  cursor: pointer;
}

.get-started-button:hover {
  opacity: 0.7;
}
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}

.logo__container {
  display: inherit;
  align-items: center;
  margin-left: 25px;
}

.header__logo {
  width: 80px;
  height: 15px;
  cursor: pointer;
}

.menu__container {
  position: relative;
  padding-right: 25px;
}

.menu__container::before {
  content: "";
  position: absolute;
  z-index: -1;
  background-image: url(/manage/static/media/bg-tablet-pattern.b6990a98.svg);
  background-size: 370px;
  background-position: left bottom;
  background-repeat: no-repeat;
  width: 318px;
  height: 350px;
  top: -30px;
  left: -272px;
}

.menu__icon {
  cursor: pointer;
}

.no-open {
  display: none;
  font-size: 0.8rem;
  font-weight: 700;
  line-height: 45px;
  color: var(--blue);
}

.open {
  position: absolute;
  top: 50px;
  right: 10px;
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0px 0px 4px 2px rgb(132 132 132 / 15%)
}

.open a {
  font-size: 1rem;
  font-weight: 700;
  line-height: 45px;
  color: var(--blue);
}

.open a:hover {
  color: var(--red);
}

.menu__button {
  display: none;
}

.button__container {
  display: none;
}

@media (min-width: 735px) {
  .header {
    justify-content: space-evenly;
    align-items: center;
  }

  .menu__container {
    padding: 0;
    margin: 0 30px;
  }

  .logo__container {
    margin-left: 40px;
  }

  .menu__container::before {
    display: none;
  }
  
  .menu__container ul {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }

  .menu__container ul a {
    margin-right: 25px;
  }

  .menu__container ul a:hover {
    color: var(--hover-color);
  }

  .menu__container ul a:last-child {
    margin-right: 0;
  }

  .menu__icon {
    display: none;
  }

  .button__container {
    display: block;
    margin-right: 40px;
  }
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 8% 0;
}

.main__hero {
  position: relative;
  display: contents;
}

.main__hero-image {
  width: 315px;
}

.better__products {
  position: relative;
  text-align: center;
}

.main__hero::before {
  content: '';
  position: absolute;
  z-index: -1;
  background-image: url(/manage/static/media/bg-tablet-pattern.b6990a98.svg);
  background-repeat: no-repeat;
  background-position: left bottom;
  background-size: 300px;
  width: 134px;
  height: 305px;
  bottom: 0;
  right: 0;
}

.better__products h1 {
  font-size: 1.8rem;
  margin: 12px 0;
}

.better__products p {
  font-size: 0.8rem;
  margin-bottom: 25px;
}

.main__whats-different {
  margin: 40px 0 0 0;
  text-align: center;
}

.main__whats-different h2 {
  font-size: 1.6rem;
  margin-bottom: 12px;
}

.main__whats-different p {
  font-size: 0.7rem;
}

.opinions__title {
  margin: 40px 0 0 0;
  font-size: 1.6rem;
}

.opinions {
  display: flex;
  align-items: center;
  width: 100%;
  height: 300px;
  overflow: hidden;
  text-align: center;
}

.move__slider {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}

.circles {
  display: flex;
  margin-bottom: 20px;
}

.circle {
  display: inherit;
  justify-content: space-between;
  width: 7px;
  height: 7px;
  margin-right: 5px;
  border: 1px solid var(--red);
  border-radius: 50%;
  cursor: pointer;
}

.circle:last-child {
  margin-right: 0;
}

.circle:active {
  background-color: var(--red);
}

.simplify__work {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; 
  width: 119%;
  height: 350px;
  margin-top: 30px;
  background-color: var(--red);
}

.simplify__work::before {
  content: '';
  position: absolute;
  z-index: 2;
  background-image: url(/manage/static/media/bg-tablet-pattern.b6990a98.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 600px;
  width: 94px;
  height: 611px;
  top: 0;
  left: 0;
  opacity: .1;
}

.simplify__work-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.simplify__work-content h2 {
  font-size: 1.8rem;
  color: var(--light-gray);
  text-align: center;
  padding: 0 40px;
}

Button.simplify__work-button {
  width: 115px;
  height: 40px;
  margin-top: 20px;
  background-color: var(--light-gray);
  color: var(--red);
  border: none;
  border-radius: 25px;
  font-size: 12px;
  font-weight: 700;
  box-shadow: 0px 3px 7px -1px hsl(0,0,0, 0.1);
  outline: none;
  cursor: pointer;
}

Button.simplify__work-button:hover {
  opacity: 0.9;
}

@media (min-width: 735px) {
  .main p {
    font-size: 0.9rem;
  }

  .main {
    display: grid;
    grid-template-areas: "better-products hero"
                         "different different"
                         "op-title op-title"
                         "opinions opinions"
                         "button button"
                         "simplify simplify";
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    margin: 40px 0 0 0;
  }
  
  .main__hero {
    grid-area: hero;
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .main__hero::before {
    display: none;
  }

  .main__hero-image {
    width: 360px;
  }
  
  .better__products {
    grid-area: better-products;
    display: flex;
    flex-direction: column;
    justify-self: center;
    text-align: left;
    width: 60%;
    margin-left: 90px;
  }

  .better__products h1 {
  font-size: 2.4rem;
  }

  .better__products p {
  font-size: 1rem;
  }

  .main__whats-different {
    grid-area: different;
    display: flex;
    align-items: baseline;
    justify-content: space-evenly;
    text-align: left;
  }
  
  .main__whats-different .intro {
    width: 30%;
  }
  
  .main__whats-different .cards {
    display: flex;
    flex-direction: column;
    width: 30%;
  }

  .opinions__title {
    grid-area: op-title;
    display: flex;
    justify-content: center;
    margin: 100px 0 30px 0;
    font-size: 1.8rem;
  }

  .opinions {
    grid-area: opinions;
  }

  .move__slider {
    display: none;
  }

  .button__main-container {
    grid-area: button;
    display: flex;
    justify-content: center;
    margin: 50px 0 100px 0;
  }
  
  .simplify__work {
    grid-area: simplify;
    height: 200px;
    width: 100%;
  }

  .simplify__work-content {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
  }

  .simplify__work-content h2 {
    text-align: left;
    width: 30%;
    margin-left: 100px;
    padding: 0;
  }

  .simplify__work::before {
    display: none;
  }

  Button.simplify__work-button {
    margin: 0;
    margin-right: 100px;
  }
}
.card {
  display: grid;
  grid-template: "left right"
                 "bottom bottom" 1fr / 40px 1fr;
  justify-content: center;
  text-align: left;
  margin-top: 40px;
}

.card__number, .card__title {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
}

.card__number {
  position: relative;
  grid-area: left;
  width: 50px;
  height: auto;
  background-color: var(--red);
  border-radius: 25px;
}

.card__number span {
  font-size: 0.9rem;
  font-weight: 700;
  color: var(--light-gray);
}

.card__title {
  grid-area: right;
  justify-content: flex-start;
  width: 100%;
  background: linear-gradient(to right, var(--pale-red) 60%, #FFFFFF);
}

.card__title h3 {
  font-size: 0.8rem;
  margin-left: 25px;
}

.card__description {
  grid-area: bottom;
  margin-top: 12px;
}

@media (min-width: 735px) {
  .card__title {
    background: none;
  }

  .card__title h3 {
    font-size: 1rem;
  }
}
.opinions__card {
  min-width: 200px;
  max-width: 300px;
  margin: 0 25px;
  background-color: var(--light-gray);
}

.opinions__card:first-child {
  padding-left: 10px;
}

.opinions__card:last-child {
  padding-right: 30px;
}

.opinions__card figure {
  margin: 30px 0 20px 0;
}

.opinions__card figure img {
  width: 80px;
}

.opinions__card h4 {
  font-size: 1rem;
}

.opinions__card blockquote {
  margin: 15px;
  font-size: 0.7rem;
}

@media (min-width: 735px) {
  .opinions__card {
    padding: 0;
  }

  .opinions__card:first-child {
  padding-left: 0;
  }

  .opinions__card figure img {
  width: 100px;
  }

  .opinions__card h4 {
    font-size: 1rem;
  }

  .opinions__card blockquote {
    margin: 15px;
    font-size: 0.8rem;
  }
}
.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-blue);
}

.footer__form {
  margin-top: 20px;
}

.footer__form form label input {
  width: 200px;
  height: 30px;
  margin-right: 8px;
  border: none;
  border-radius: 25px;
  outline: none;
}

.footer__form form label input::-webkit-input-placeholder {
  padding-left: 15px;
  color: var(--gray);
}

.footer__form form label input:-ms-input-placeholder {
  padding-left: 15px;
  color: var(--gray);
}

.footer__form form label input::placeholder {
  padding-left: 15px;
  color: var(--gray);
}

.footer__form button {
  width: 60px;
  height: 30px;
  border: none;
  border-radius: 25px;
  outline: none;
  background-color: var(--red);
  color: var(--light-gray);
  font-weight: 700;
  cursor: pointer;
}

.footer__form button:hover {
  opacity: 0.7;
}

.email__error {
  font-size: 0.7rem;
  color: var(--red);
  margin-top: 10px;
}

.footer__nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
  max-width: 430px;
  margin: 40px 0 0 0;
}

.footer__nav ul {
  margin: 0 0 40px 0;
  padding: 0;
  text-align: left;
  list-style: none;
}

.footer__nav ul a {
  font-size: 0.9rem;
  color: white;
  text-decoration: none;
  line-height: 35px;
}

.footer__nav ul a:hover {
  color: var(--red);
}

.footer__social-links {
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.footer__social-links figure {
  margin-right: 30px;
}

.footer__social-links figure:last-child {
  margin-right: 0;
}

.footer__icon {
  width: 30px;
  cursor: pointer;
}

.footer__icon:hover {
  -webkit-filter: invert(0.35) sepia(0.5) saturate(50) hue-rotate(331.2deg) brightness(0.98);
          filter: invert(0.35) sepia(0.5) saturate(50) hue-rotate(331.2deg) brightness(0.98);
}

.footer__logo {
  margin-top: 40px;
}

.copyright {
  font-size: 0.7rem;
  margin: 30px 0 15px 0;
}

@media (min-width: 850px) {
  .footer {
    display: grid;
    grid-template: "logo nav nav form"
                   "social-links nav nav copyright";
    width: 100%;
    justify-content: space-evenly;
  }

  .footer__form {
    grid-area: form;
  }

  .footer__form button {
    margin-top: 15px;
  }
  
  .footer__nav {
    grid-area: nav;
  }

  .footer__nav ul {
    padding: 0 40px;
  }
  
  .footer__social-links {
    grid-area: social-links;
  }

  .footer__icon {
  width: 20px;
  cursor: pointer;
  }
  
  .footer__logo {
    grid-area: logo;
  }
  
  .copyright {
    grid-area: copyright;
  }
}
